import { LitElement, html, css } from '@polymer/lit-element'
import {anchorRoute, gotoRoute} from './../Router'
import Auth from './../Auth'
import App from './../App'


customElements.define('va-app-header', class AppHeader extends LitElement {
  constructor(){
    super()    
  }


  static get properties(){
    return {
      title: {
        type: String
      },
      user: {
        type: Object
      }
    }
  }


  firstUpdated(){
    super.firstUpdated()
    this.navActiveLinks()    
  }


  navActiveLinks(){
    const currentPath = window.location.pathname
    const navLinks = this.shadowRoot.querySelectorAll('.app-top-nav a, .app-side-menu-items a')
    navLinks.forEach(navLink => {
      if(navLink.href.slice(-1) == '#') return
      if(navLink.pathname === currentPath){    
        navLink.classList.add('active')
      }
    })
  }


  hamburgerClick(){  
    const appMenu = this.shadowRoot.querySelector('.app-side-menu')
    appMenu.show()
  }
 
  menuClick(e){
    e.preventDefault()
    const pathname = e.target.closest('a').pathname
    const appSideMenu = this.shadowRoot.querySelector('.app-side-menu')
    // hide appMenu
    appSideMenu.hide()
    appSideMenu.addEventListener('sl-after-hide', () => {
      // goto route after menu is hidden
      gotoRoute(pathname)
    })
  }


  render(){    
    return html`
    <style>      
      * {
        box-sizing: border-box;
      }
      .app-header {
        background: var(--brand-color);
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        height: var(--app-header-height);
        color: #fff;
        display: flex;
        z-index: 9;
        box-shadow: 4px 0px 10px rgba(0,0,0,0.2);
        align-items: center;
      }
     


      .app-header-main {
        flex-grow: 1;
        display: flex;
        align-items: center;
      }


      .app-header-main::slotted(h1){
        color: #fff;
      }


      .app-logo a {
        color: #fff;
        text-decoration: none;
        font-weight: bold;
        font-size: 1.2em;
        padding: .6em;
        display: inline-block;        
      }


      .app-logo img {
        width: 90px;
      }
     
      .hamburger-btn::part(base) {
        color: #fff;
      }


      .app-top-nav {
        display: flex;
        height: 100%;
        align-items: center;
      }


      .app-top-nav a {
        display: inline-block;
        padding: .8em;
        text-decoration: none;
        color: #fff;
      }
     
      .app-side-menu-items a {
        display: block;
        padding: .5em;
        text-decoration: none;
        font-size: 1.3em;
        color: #333;
      }


      .app-side-menu-logo {
        width: 120px;
        margin-bottom: 1em;
        position: absolute;
        top: 2em;
        left: 1.5em;
      }


      .page-title {
        color: var(--app-header-txt-color);
        margin-right: 0.5em;
        font-size: var(--app-header-title-font-size);
      }


      /* active nav links */
      .app-top-nav a.active,
      .app-side-menu-items a.active {
        font-weight: bold;
      }


      /* RESPONSIVE - MOBILE ------------------- */
      @media all and (max-width: 768px){      
       
        .app-top-nav {
          display: none;
        }
      }


    </style>


    <header class="app-header">
      <sl-icon-button class="hamburger-btn" name="list" @click="${this.hamburgerClick}" style="font-size: 1.5em;"></sl-icon-button>      
     
      <div class="app-header-main">
        ${this.title ? html`
          <h1 class="page-title">${this.title}</h1>
        `:``}
        <slot></slot>
      </div>


      <nav class="app-top-nav">
        <a href="/" @click="${anchorRoute}">
        <img src="/images/icons_home2.svg" alt="Home" style="width: 16px; height: 16px; margin-right: 10px;">
        Home</a>
        ${this.user.accessLevel ==2 ? html`
        <a href="/newService" @click="${anchorRoute}">
        <img src="/images/icons_addservice.svg" alt="Add Service" style="width: 16px; height: 16px; margin-right: 10px;">
        Add Service</a>    
        ` : ``}    
        
        <sl-dropdown>
          <a slot="trigger" href="#" @click="${(e) => e.preventDefault()}">
            <sl-avatar style="--size: 24px;" image=${(this.user && this.user.avatar) ? `${App.apiBase}/images/${this.user.avatar}` : ''}></sl-avatar> ${this.user && this.user.firstName}
          </a>
          <sl-menu>            
          <sl-menu-item @click="${() => gotoRoute('/profile')}">
            <img src="/images/icons_profile.svg" alt="Profile" style="width: 16px; height: 16px; margin-right: 10px;">
            Profile
          </sl-menu-item>
          
          <sl-menu-item @click="${() => gotoRoute('/editProfile')}">
            <img src="/images/icons_editprofile.svg" alt="Edit Profile" style="width: 16px; height: 16px; margin-right: 10px;">
            Edit Profile
          </sl-menu-item>
          
          <sl-menu-item @click="${() => Auth.signOut()}">
            <img src="/images/icons_logout.svg" alt="Sign Out" style="width: 16px; height: 16px; margin-right: 10px;">
            Sign Out
          </sl-menu-item>
        </sl-menu>
        
        </sl-dropdown>
      </nav>
    </header>


    <sl-drawer class="app-side-menu" placement="left">
    <img class="app-side-menu-logo" src="/images/logo.svg">
    <nav class="app-side-menu-items">
    <a href="/" @click="${this.menuClick}">
    <img src="/images/icons_home.svg" alt="Home" style="width: 22px; height: 22px; margin-right: 20px;">Home
  </a>
      
      ${this.user.accessLevel === 1
        ? html`
        <a href="/services" @click="${this.menuClick}">
        <img src="/images/icons_services.svg" alt="Services" style="width: 22px; height: 22px; margin-right: 20px;">
        Services
        </a>
        
        <a href="/serviceproviders" @click="${this.menuClick}">
        <img src="/images/icons_serviceproviders.svg" alt="Service Providers" style="width: 22px; height: 22px; margin-right: 20px;">
        Service Providers
        </a>
          
        <a href="/favouriteServices" @click="${this.menuClick}">
        <img src="/images/icons_favourites.svg" alt="Favourites" style="width: 22px; height: 22px; margin-right: 20px;">
        Favourites
        </a>

        <a href="/bookings" @click="${this.menuClick}">
        <img src="/images/icons_bookings.svg" alt="Bookings" style="width: 22px; height: 22px; margin-right: 20px;">
        Bookings
        </a>

        `
        : this.user.accessLevel === 2
        ? html`
        <a href="/myServices" @click="${this.menuClick}">
        <img src="/images/icons_services.svg" alt="My Services" style="width: 22px; height: 22px; margin-right: 20px;">
        My Services
        </a>
      
        <a href="/bookings" @click="${this.menuClick}">
        <img src="/images/icons_bookings.svg" alt="My Bookings" style="width: 22px; height: 22px; margin-right: 20px;">
        My Bookings
        </a>
        `
        : html``
      }
    
      <a href="/profile" @click="${this.menuClick}">
      <img src="/images/icons_profile.svg" alt="Profile" style="width: 22px; height: 22px; margin-right: 20px;">
      Profile</a>
      <a href="#" @click="${() => Auth.signOut()}">
      <img src="/images/icons_logout.svg" alt="Sign Out" style="width: 22px; height: 22px; margin-right: 20px;">
      Sign Out</a>
    </nav>  
  </sl-drawer>
    `
  }
 
})





